const actions = {
  getDepartments: () => ({
    type: "GET_DEPARTMENTS_REQUEST",
  }),
  storeDepartment: body => ({
    type: "STORE_DEPARTMENT_REQUEST",
    payload: { body: body }
  }),
  showDepartment: id_department => ({
    type: "SHOW_DEPARTMENT_REQUEST",
    payload: { id_department: id_department }
  }),
  updateDepartment: (id_department, body) => ({
    type: "UPDATE_DEPARTMENT_REQUEST",
    payload: { id_department: id_department, body: body }
  }),
  activateDepartment: id_department => ({
    type: "ACTIVATE_DEPARTMENT_REQUEST",
    payload: { id_department: id_department }
  }),
  deactivateDepartment: id_department => ({
    type: "DEACTIVATE_DEPARTMENT_REQUEST",
    payload: { id_department: id_department }
  }),
  clearDepartment: () => ({
    type: "CLEAR_DEPARTMENT"
  }),
  clearSuccessDepartment: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
