import React, { Component } from "react";
import Popconfirms from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import moment from "moment";
import {
  Form,
  Input,
  Col,
  Row,
  Divider,
  TreeSelect,
  Alert,
  DatePicker,
  Select,
} from "antd";
import getLanguage from "../../../App/MyLanguange"
const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class DepartmentForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props.department);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { user } = this.props.auth;
    const department = this.props.department ? this.props.department : [];
    const { loadingSubmit, loading } = department;
    const { activate, deactivate } = this.props;
    let data = department.department ? department.department : [];
    //console.log(data);
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const used = data.is_used;
    const error = department.errorField ? department.errorField : [];

    const loop = (array, except_id) =>
      array.map(data => {
        let perData;
        if (data.is_active === 0) {
          perData = {
            title: data.department_code + " (" + data.department_name + ")",
            value: data.id_department,
            key: data.id_department,
            disabled: true,
          }
        }
        else if (data.id_department != except_id) {
          perData = {
            title: data.department_code + " (" + data.department_name + ")",
            value: data.id_department,
            key: data.id_department,
            children: data.children ? loop(data.children, except_id) : null
          }
        }
        else {
          perData = {
            title: data.department_code + " (" + data.department_name + ")",
            value: data.id_department,
            key: data.id_department,
            disabled: true,
          }
        }


        return perData
      })



    const formAssetLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormAssetLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="departments.update.subtitle.data" />
        </Divider>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="departments.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : null}

        <Row>
          <Col span={24}>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="departments.parent" />}
            >
              {getFieldDecorator("id_department_parent", {
                initialValue: data.id_department_parent,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <TreeSelect
                  style={{ width: "100%" }}
                  allowClear={true}
                  showSearch={true}
                  treeNodeFilterProp="title"
                  dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
                  treeData={loop(this.props.department.dropdown_update, data.id_department)}
                  treeDefaultExpandAll
                // onChange={this.onChange}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="departments.code" />}
              validateStatus={
                error.department_code || getFieldError("department_code")
                  ? "error"
                  : null
              }
              help={
                error.department_code
                  ? error.department_code
                  : getFieldError("department_code")
              }
            >
              {getFieldDecorator("department_code", {
                initialValue: data.department_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  // addonBefore={prefix}
                  disabled={used ? true : false}
                />
              )}
            </Form.Item>


            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="departments.name" />}
            >
              {getFieldDecorator("department_name", {
                initialValue: data.department_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input
                disabled={used ? true : false}
              />)}
            </Form.Item>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="departments.employee" />}
            >
              {getFieldDecorator("id_employee_manager", {
                initialValue: data.id_employee_manager,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <Select
                  //loading={this.props.department.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                //disabled={used ? true : false}
                >
                  {this.props.department.employees.map((data, key) => (
                    <Option key={key} value={data.id_employee}>
                      {data.employee_code + " (" + data.first_name + (data.last_name ? " " + data.last_name : "") + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formAssetLayout}
              label={<IntlMessages id="accounts.openingBalanceDate" />}
              style={{ display: "none" }}
            >
              {getFieldDecorator("start_date", {
                initialValue: data.start_date ? moment(data.start_date, "YYYY-MM-DD") : null
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  allowClear={true}
                  // showTime={{ format: "HH:mm:ss" }}
                  format="DD MMMM YYYY"
                //disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description
              })(
                <TextArea autosize={{ minRows: 2 }} />
              )}
            </Form.Item>

          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <InputGroup compact>
              <Col>
                <Form.Item {...tailFormAssetLayout}>
                  <Button
                    type="primary"
                    htmlType="submit"
                    style={{ marginRight: "15px" }}
                    loading={loadingSubmit ? true : false}
                  >
                    <IntlMessages id="button.submit" />
                  </Button>
                </Form.Item>
              </Col>
              <Col>
                {data.is_active ? (
                  <Form.Item {...tailFormAssetLayout}>
                    <Popconfirms
                      title={
                        <IntlMessages id="departments.popconfirm.deactivate" />
                      }
                      onConfirm={e => {
                        e.preventDefault();
                        deactivate(data.id_department);
                      }}
                      okText={getLanguage({ id: "yes" })}
                      cancelText={getLanguage({ id: "no" })}
                    >
                      <Button
                        type="danger"
                        loading={
                          loadingSubmit || loading ? true : false
                        }
                        disabled={used ? true : false}
                      >
                        <IntlMessages id="tooltip.deactivate" />
                      </Button>
                    </Popconfirms>
                  </Form.Item>
                ) : (
                    <Form.Item {...tailFormAssetLayout}>
                      <Popconfirms
                        title={
                          <IntlMessages id="departments.popconfirm.activate" />
                        }
                        onConfirm={e => {
                          e.preventDefault();
                          activate(data.id_department);
                        }}
                        okText={getLanguage({ id: "yes" })}
                        cancelText={getLanguage({ id: "no" })}
                      >
                        <Button
                          type="default"
                          style={{ backgroundColor: "#00B16A", color: "#FFFFFF", }}
                          loading={
                            loadingSubmit || loading ? true : false
                          }
                          disabled={used ? true : false}
                        >
                          <IntlMessages id="tooltip.activate" />
                        </Button>
                      </Popconfirms>
                    </Form.Item>
                  )}
              </Col>
            </InputGroup>
          </Col>
        </Row>
      </Form>
    );
  }
}

const WrappedDepartmentForm = Form.create({ name: "update_department" })(DepartmentForm);

export default WrappedDepartmentForm;
