import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import notification from "../../../../components/notification";
import { Input, Tree, Row, Col, Divider, Empty, Spin } from "antd";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/department/actions";
import CardWrapper, { Box } from "../../../App/app.style";
import CreateForm from "./createForm";
import UpdateForm from "./updateForm";
import getLanguage from "../../../App/MyLanguange";

// const Option = Select.Option;
const Search = Input.Search;
const { TreeNode } = Tree;

const redirectTo = "/dashboard/setup/menus";

// Searching Asset
const getParentKey = (key, tree) => {
  let parentKey;
  for (let i = 0; i < tree.length; i++) {
    const node = tree[i];
    if (node.children) {
      if (node.children.some(department => department.id_department === key)) {
        parentKey = node.id_department;
      } else if (getParentKey(key, node.children)) {
        parentKey = getParentKey(key, node.children);
      }
    }
  }
  return parentKey;
};

class Department extends Component {
  state = {
    expandedKeys: [],
    searchValue: "",
    autoExpandParent: true,
    id_department: null,
    status: 0
    // 0 empty , 1 create , 2 update
  };

  // Get All data diawal
  componentDidMount() {
    const { getDepartments } = this.props;
    getDepartments();
  }

  // Display notif
  componentDidUpdate() {
    // Display error notif
    if (
      !this.props.department.loadingSubmit &&
      !this.props.department.loadingDepartment &&
      !this.props.department.loadingDepartments &&
      this.props.department.error
    ) {
      notification("error", this.props.department.error);
      this.props.clearError();
    }

    // Display success notif
    if (
      !this.props.department.loadingSubmit &&
      !this.props.department.loadingDepartment &&
      !this.props.department.loadingDepartments &&
      this.props.department.success
    ) {
      notification("success", this.props.department.success);
      this.props.clearSuccessDepartment();

      // Clear state new Department
      this.setState({
        ...this.state,
        id_department: this.props.department.department
          ? this.props.department.department.id_department
          : null
      });
    }
  }

  // Handle change search
  handleSearchChange = e => {
    const { departments_list, departments } = this.props.department;
    const value = e.target.value;
    const expandedKeys = departments_list.map(department => {
      const name =
        department.department_code + " " + department.department_name;
      if (name.toLowerCase().indexOf(value.toLowerCase()) > -1) {
        return getParentKey(department.id_department, departments);
      }
      return null;
    });

    this.setState({
      expandedKeys,
      searchValue: value,
      autoExpandParent: true
    });
  };

  // Handle Add Button
  handleAddButton = () => {
    const { clearDepartment } = this.props;
    this.setState({
      id_department: null,
      status: 1
    });
    clearDepartment();
  };

  // Handle select tree
  handleSelectTree = e => {
    const { showDepartment } = this.props;
    this.props.clearErrorField();

    const value = e[0] ? e[0] : "";

    if (value) {
      showDepartment(value);
    }

    this.setState({
      ...this.state,
      id_department: value ? value : null,
      status: value ? 2 : 0
    });
  };

  // Handle expand tree
  onExpand = expandedKeys => {
    this.setState({
      expandedKeys,
      autoExpandParent: false
    });
  };

  // Handle submit Create
  handleSubmitCreate = value => {
    const { storeDepartment } = this.props;
    // console.log(value);
    if (value.start_date) {
      value.start_date = value.start_date.format("YYYY-MM-DD");
    }
    storeDepartment(value);
  };

  handleSubmitUpdate = value => {
    const { updateDepartment } = this.props;
    const { id_department } = this.state;
    if (value.start_date) {
      value.start_date = value.start_date.format("YYYY-MM-DD");
    }
    updateDepartment(id_department, value);
  };

  // Handle Activate department
  handleActivate = id_department => {
    const { activateDepartment } = this.props;
    // console.log(id_department);
    activateDepartment(id_department);
  };

  // Handle dectivate department
  handleDeactivate = id_department => {
    const { deactivateDepartment } = this.props;
    // console.log(id_department);
    deactivateDepartment(id_department);
  };

  render() {
    // console.log("/\\/");
    // console.log(this.props.department);
    const { department, departments } = this.props.department;
    const {
      loadingSubmit,
      loadingDepartment,
      loadingDepartments
    } = this.props.department;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const checkLoading = () => {
      if (loadingDepartments) {
        return true;
      }
      return false;
    };

    const {
      searchValue,
      expandedKeys,
      autoExpandParent
      // newDepartment
    } = this.state;

    const loop = data =>
      data.map(department => {
        let name =
          department.department_code + " (" + department.department_name + ")";
        const key = name;
        const index = key.toLowerCase().indexOf(searchValue.toLowerCase());
        const beforeStr = name.substr(0, index);
        const str = name.substr(index, searchValue.length);
        const afterStr = name.substr(index + searchValue.length);
        const title =
          index > -1 ? (
            <span>
              {beforeStr}
              <span style={{ color: "#000000", fontWeight: "bold" }}>
                {str}
              </span>
              {afterStr}
            </span>
          ) : (
            <span>{name}</span>
          );
        if (department.children) {
          return (
            <TreeNode
              key={department.id_department}
              title={
                department.is_active === 0 ? (
                  <span style={{ color: "#f5222d" }}>{title}</span>
                ) : (
                  title
                )
              }
            >
              {loop(department.children)}
            </TreeNode>
          );
        }
        return <TreeNode key={department.id_department} title={title} />;
      });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="departments.pageHeader" />}
            style={{ margin: 0 }}
          >
            {permissions.includes("Create Department") ? (
              <Col span={12}>
                <Row type="flex" justify="end">
                  <Col>
                    <Button type="primary" onClick={this.handleAddButton}>
                      <IntlMessages id="departments.add" />
                    </Button>
                    <Button
                      onClick={() => this.props.history.replace(redirectTo)}
                      style={{ marginLeft: "5px" }}
                    >
                      <IntlMessages id="button.back" />
                    </Button>
                  </Col>
                </Row>
              </Col>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            {/* <InputGroup compact> */}
            <Col span={12}>
              <Col>
                <Search
                  placeholder={getLanguage({ id: "departments.pageHeader" })}
                  disabled={checkLoading()}
                  allowClear
                  onChange={this.handleSearchChange}
                />
              </Col>
            </Col>
            {/* </InputGroup> */}
          </div>

          <CardWrapper>
            <Col span={12}>
              <Col span={23}>
                {checkLoading() ? (
                  <div className="spinStyle">
                    <Spin />
                  </div>
                ) : departments.length === 0 ? (
                  <Empty description={<IntlMessages id="noData" />} />
                ) : (
                  <Tree
                    showIcon
                    showLine
                    onExpand={this.onExpand}
                    expandedKeys={expandedKeys}
                    autoExpandParent={autoExpandParent}
                    onSelect={this.handleSelectTree}
                  >
                    {loop(departments)}
                  </Tree>
                )}
              </Col>
              <Col span={1}>
                <Divider type="vertical" style={{ height: "100vh" }} />
              </Col>
            </Col>
            <Col span={12}>
              {loadingDepartment && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin />
                </div>
              ) : this.state.status === 2 ? (
                <UpdateForm
                  submit={this.handleSubmitUpdate}
                  department={this.props.department}
                  auth={this.props.auth}
                  activate={this.handleActivate}
                  deactivate={this.handleDeactivate}
                />
              ) : this.state.status === 1 ? (
                <CreateForm
                  submit={this.handleSubmitCreate}
                  department={this.props.department}
                  auth={this.props.auth}
                />
              ) : (
                <Empty
                  description={<IntlMessages id="departments.noSelection" />}
                />
              )}
            </Col>
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { department: state.department, auth: state.auth };
};

export default compose(connect(mapStateToProps, actions))(Department);
